import { mapGetters } from 'vuex'

export default {
  async asyncData({ $strapi, store, error }) {
    try {
      const response = await $strapi.$productos.find([['category.name', 'men']])
      store.commit('setMenProducts', response)
    } catch (e) {
      error(e)
    }
  },
  data() {
    return {
      menProds: [],
    }
  },
  computed: {
    ...mapGetters(['menProducts']),
  },
}
